.wrapper {
    border-bottom: 3px solid #000;
    padding: 20px 0;
}
.container {
    margin: 0 auto;
	padding: 0 20px;
}
.button_container {
	max-width: 600px;
	margin: 0 auto;
}
.button {
	box-shadow:inset 0px 1px 0px 0px #bbdaf7;
	background:linear-gradient(to bottom, #35f02f 5%, #218d00 100%);
	background-color:#35f02f;
	border-radius:6px;
	border:1px solid #03a325;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-family:Arial;
	font-weight:bold;
	padding:6px 10px;
	text-decoration:none;
}
.button:hover {
	background:linear-gradient(to bottom, #218d00 5%, #35f02f 100%);
	background-color:#218d00;
}
.button:active {
	position:relative;
	top:1px;
}
.button_container {
    text-align: right;
}
.button:disabled {
    background:rgb(114, 114, 114);
    border: 1px solid #616161;
    text-shadow: none;
}
.button:disabled:hover {
    cursor: auto;
}